import goodsList from '../../pageb2c/list.vue'
let cacheKey = "index_index";
export default {
	components: {
		goodsList
	},
	data() {
		return {
			pageData: {},
			pageLoad: false,
			keyword: "",
			showList: {
				'bmList': {
					'icon': 'icon-likefill ',
					'name': '必买好货',
					'description': '大家都会心动的产品',
					'type': 'bimai',
					'check': '0'
				},
				'recList': {
					'icon': 'icon-moneybag',
					'name': '猜你喜欢',
					'description': 'Guess You Like It',
					'type': 'recommend',
					'check': '1'
				},
				'hotList': {
					'icon': 'icon-hot_light',
					'name': '热销商品',
					'description': 'Hot Goods',
					'type': 'hot',
					'check': '1'

				}
			}

		}
	},
	onLoad: function() {
		// if (!this.getCache()) {
		uni.showNavigationBarLoading();
		this.getPage();
		// }
	},

	onReachBottom: function() {
		console.log('reachBottom')
	},
	onPullDownRefresh: function() {
		this.refresh()
	},

	methods: {
		getCache: function() {
			let val = uni.getStorageSync(cacheKey)
			if (!val) return false;
			let time = Date.parse(new Date()) / 1000;
			if (val.expire < time) {
				return false
			}
			let v = JSON.parse(val);
			this.pageLoad = v.pageLoad;
			this.pageData = v.pageData;
			this.keyword = v.keyword;
			return true
		},
		setCache: function() {
			let val = {
				pageLoad: this.pageData,
				pageData: this.pageData,
				keyword: this.keyword,
				expire: Date.parse(new Date()) / 1000 + 300
			}
			uni.setStorageSync(cacheKey, JSON.stringify(val))
		},
		goUrl: function(url) {
			uni.navigateTo({
				url
			})
		},
		search: function() {
			uni.navigateTo({
				url: "../../pageb2c/b2c_search/index?keyword=" + this.keyword
			})
		},
		getPage: function() {
			let that = this;
			that.app.get({
				url: this.app.apiHost + "/module.php?m=b2c&ajax=1",
				success: (res) => {
					that.pageData = res.data;
					that.pageLoad = true;
					uni.hideNavigationBarLoading();
					that.setCache()
				}
			})
		},
		refresh: function() {
			this.getPage();
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000)
		},
	}
}
